export default function BuildSteps() {
  const steps = [
    {
      number: 1,
      title: "Chipset & Budget",
      description: "Select your budget and your choice of AMD or Intel CPUs."
    },
    {
      number: 2,
      title: "Pick Parts",
      description: "Pick your components and accessories based on your performance and aesthetic needs."
    },
    {
      number: 3,
      title: "We Build & Ship",
      description: "Our experts build and ship your product directly to you with a two-day build time."
    }
  ];

  return (
    <div className="bg-gradient-to-b from-purple-900 to-black py-16">
      <div className="container mx-auto px-4">
        <div className="grid md:grid-cols-3 gap-8">
          {steps.map((step) => (
            <div 
              key={step.number}
              className="bg-gradient-to-br from-blue-900 to-purple-900 rounded-xl p-8 text-center text-white"
            >
              <div className="w-12 h-12 rounded-full border-2 border-white flex items-center justify-center mx-auto mb-6">
                <span className="text-2xl font-bold">{step.number}</span>
              </div>
              <h3 className="text-2xl font-bold mb-4">{step.title}</h3>
              <p className="text-gray-300">{step.description}</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
} 