import Image from 'next/image';
import Link from 'next/link';

export default function Hero() {
  return (
    <div className="bg-gradient-to-br from-blue-900 via-purple-900 to-black text-white py-16">
      <div className="container mx-auto px-4">
        <h1 className="text-5xl font-bold text-center mb-12">Let's Build Together!</h1>
        
        <div className="flex flex-col lg:flex-row items-center justify-between gap-12">
          <div className="lg:w-1/2">
            <Image
              src="/images/gaming-pc.png"
              alt="Custom Gaming PC"
              width={600}
              height={600}
              className="object-contain"
            />
          </div>
          
          <div className="lg:w-1/2 space-y-8">
            <h2 className="text-4xl font-bold mb-6">Customize Your Gaming PC</h2>
            
            <div className="space-y-6">
              <div className="flex items-start gap-4">
                <div className="w-12 h-12">
                  <Image
                    src="/icons/parts.svg"
                    alt="Curated Parts"
                    width={48}
                    height={48}
                  />
                </div>
                <div>
                  <h3 className="text-xl font-semibold">Curated Parts</h3>
                  <p className="text-gray-300">Take your pick of top-of-the-line components like the latest GPUs, CPUs, and more.</p>
                </div>
              </div>

              <div className="flex items-start gap-4">
                <div className="w-12 h-12">
                  <Image
                    src="/icons/fps.svg"
                    alt="FPS Guaranteed"
                    width={48}
                    height={48}
                  />
                </div>
                <div>
                  <h3 className="text-xl font-semibold">FPS Guaranteed</h3>
                  <p className="text-gray-300">View popular games and their FPS performance alongside your build.</p>
                </div>
              </div>
            </div>

            <Link 
              href="/customize"
              className="inline-block px-8 py-3 bg-purple-600 hover:bg-purple-700 rounded-lg font-semibold transition-colors"
            >
              Customize a PC
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
} 