import Image from 'next/image';
import Link from 'next/link';

export default function Categories() {
  const categories = [
    {
      icon: "/icons/gaming.svg",
      title: "Gaming",
      description: "Whether you want to play popular games, get eye-watering visuals, or hit peak FPS, our Prebuilts got you covered from mid-range to top-of-the-line GPUs and CPUs.",
      link: "/player-one"
    },
    {
      icon: "/icons/streaming.svg",
      title: "Streaming",
      description: "Ready to go live with your stream? We offer powerful CPUs, GPUs, and plenty capacity and performance so you can play and stream without taking a performance hit.",
      link: "/player-two"
    },
    {
      icon: "/icons/creating.svg",
      title: "Creating",
      description: "Multitask like a pro with maxed out performance parts in our newest cases, allowing you to tackle more applications and increase your workflow. Our gaming PCs go beyond gaming.",
      link: "/player-three"
    }
  ];

  return (
    <div className="bg-gray-100 py-16">
      <div className="container mx-auto px-4">
        <h2 className="text-4xl font-bold text-center mb-12">Build Your Dream PC</h2>
        
        <div className="grid md:grid-cols-3 gap-8">
          {categories.map((category) => (
            <div 
              key={category.title}
              className="bg-white rounded-xl p-8 text-center shadow-lg"
            >
              <div className="w-16 h-16 mx-auto mb-6">
                <Image
                  src={category.icon}
                  alt={category.title}
                  width={64}
                  height={64}
                />
              </div>
              <h3 className="text-2xl font-bold mb-4">{category.title}</h3>
              <p className="text-gray-600 mb-6">{category.description}</p>
              <Link
                href={category.link}
                className="inline-block px-6 py-3 bg-purple-600 text-white rounded-lg hover:bg-purple-700 transition-colors"
              >
                Shop {category.title}
              </Link>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
} 